<template>
	<div>
		<el-breadcrumb separator="/">
			<el-breadcrumb-item>系统管理</el-breadcrumb-item>
			<el-breadcrumb-item>评分</el-breadcrumb-item>
		</el-breadcrumb>
		<el-card>
			<el-table :data="tableData" border stripe="" style="width: 100%">
				<el-table-column prop="name" label="姓名" width="300"> </el-table-column>
				<el-table-column prop="number" label="学号" width="300"> </el-table-column>
				<el-table-column prop="acdemic" label="学院" width="300"> </el-table-column>
				<el-table-column prop="event" label="活动" width="300"> </el-table-column>
				<el-table-column label="操作">
					<template slot-scope="scope">
						<el-button type="primary" icon="el-icon-edit" @click="editclick(scope.row)">评分</el-button>
						<el-select v-model="info.score" placeholder="请选择">
							<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
							</el-option>
						</el-select>
					</template>
				</el-table-column>
			</el-table>
		</el-card>

		<el-dialog title="评分" :visible.sync="dialogVisible" width="40%">
			<el-form :model="info" :rules="rules" ref="scoreform" label-width="100px">
				<el-form-item label="姓名">
					<el-col :span="16">
						<el-input disabled v-model="info.name"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="学号">
					<el-col :span="16">
						<el-input disabled v-model="info.number"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="学院">
					<el-col :span="16">
						<el-input disabled v-model="info.acdemic"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="活动">
					<el-col :span="16">
						<el-input disabled v-model="info.event"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="等级" prop="score">
					<el-select v-model="info.score" placeholder="请选择">
						<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="评价" prop="evaluate">
					<el-col :span="16">
						<el-input type="textarea" v-model="info.evaluate"></el-input>
					</el-col>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible=false">取消</el-button>
				<el-button type="primary" @click="submitForm('scoreform')">上传</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		name: 'score',
		data() {
			var checkscore =(rules,value,cb) =>
			{
				this.score=value;
				if(value=='不及格')
				{
					this.must=true
				}
				else
				{
					this.must=false
				}
				if(!value)
				{
					cb(new Error('请输入等级'))
				}
				else
				cb()
			};
			var check = (rules, value, cb) => {
				if(this.must==true&&!value)
				{
					cb(new Error('请输入评价'))
				}
				else
				{		
					cb()
				}
				
			};
			return {
				options: [{
					value: '优秀',
					label: '优秀'
				}, {
					value: '良好',
					label: '良好'
				}, {
					value: '一般',
					label: '一般'
				}, {
					value: '及格',
					label: '及格'
				}, {
					value: '不及格',
					label: '不及格'
				}],
				dialogVisible: false,
				info: {
					name: '',
					number: '',
					acdemic: '',
					event: '',
					score: '',
					evaluate: ''
				},
				tableData: [{
					name: '张三',
					number: '123',
					acdemic: '控计',
					event: '扫地'
				},
				{
					name: '李四',
					number: '123',
					acdemic: '控计',
					event: '扫地'
				
				}
				],
				rules: {
					score: [{
						validator: checkscore,
						trigger: 'blur'
					}],
					evaluate: [{
						validator: check,
						trigger: 'blur'
					}]

				},

			}
		},
		methods: {
			editclick(row) {
				this.info = row,
					this.dialogVisible = true
			},
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						alert('submit!');
						this.dialogVisible = false
					} else {
						console.log('error submit!!');
						return false;
					}

				});
			}

		}
		
	}
</script>

<style>
	.el-breadcrumb {
		margin-bottom: 15px;
	}
</style>
